"use client";

import React from "react";
import { useRouter } from "next/navigation";
import Icon from "./Icon";

const ModuleCard = ({ module }: { module: any }) => {
  const router = useRouter();

  const handleClick = () => {
    if (module.status !== "locked") {
      router.push(`/dashboard/progress/module/${module.id}`); // Ruta correcta
    }
  };

  return (
    <div
      className={`flex items-center justify-between p-2 rounded cursor-pointer ${
        module.status === "locked" ? "bg-gray-200 cursor-not-allowed" : "bg-white"
      }`}
      onClick={handleClick}
    >
      <span className={`text-gray-800 ${module.status === "locked" && "text-gray-400"}`}>
        {module.name}
      </span>
      <Icon name={module.status === "completed" ? "check" : module.status === "active" ? "arrow" : "lock"} />
    </div>
  );
};

export default ModuleCard;
